.choose-city {
  display: flex;
  padding: 10px 1.5rem;
}
.choose-city input {
  flex: 1;
  height: 30px;
  border: 1px solid #e0e0e0;
  padding: 0 1rem;
}
.choose-city button {
  margin-left: 1rem;
  border: none;
  padding: 0 1.5rem;
  background-color: #66d3ff;
  color: #ffffff;
  height: 30px;
}



